.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
  }
  

.about {
  top: -27rem;
  position: relative;
}

  .about__me {
    width: 40%;
    margin: 0 auto 5rem;
    border-radius: 2rem;
    background: black;
  }
  
  .about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
  }
  
  .about__me-image:hover {
    transform: rotate(0);
  }
  
 
  .about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-black);
  }
  
  /* ================ Medium Device Media Queries ================= */
  @media screen and (max-width: 1024px) {
    .about__container {
      grid-template-columns: 1fr;
      gap: 0;
    }
  
    .about__me {
      width: 50%;
      margin: 2rem auto 4rem;
    }
  
    .about__content p {
      margin: 1rem 0 1.5rem;
    }
  }
  
  /* ================ Small Device Media Queries ================= */
  @media screen and (max-width: 600px) {
    .about__me {
      width: 65%;
      margin: 0 auto 3rem;
    }
  
    .about{
      margin-top: 23rem;
    }

    .about__content {
      text-align: center;
    }
  
    .about__content p {
      margin: 1.5rem 0;
    }
  }
  
